body {
  width: 100%;
  height: 100vh;
  margin: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0px;
}

a {
  background-color: black;
  color: white;
}

.Container {
  font-size: 60px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Center {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.App__Button {
  border-radius: 5px;
  padding: 10px;
  padding-top: 5px;
  margin-top: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
.span {
  font-size: 25px;
  margin-right: 10px;
  margin-left: 10px;
}
.root {
  display: flex;
  justify-content: space-between;
  width: 90%;
}
.Header__container {
  height: 11vh;
  display: flex;
  justify-content: space-between;
  background-color: black;
  color: white;
  padding: 20px;
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-height: 100vh;
}
.center {
  text-align: center;
}
.fade-in {
  animation: fadeIn;
  animation-duration: 0.6s;
}
button {
  font-size: 30px;
}
.small {
  font-size: 20px;
}
.full {
  height: 85vh;
}
.half {
  width: 60vw;
  height: 65vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.border {
  border: 3px solid aqua;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.bottom {
  position: sticky;
  bottom: 10px;
}
